export default {
	data() {
		return {
			newMobile: '',
			newCode: '',
			code: '获取验证码',
			disabled: false,
			msgCode: '',
			num: 60,
		}
	},
	onLoad() {

	},
	methods: {
		// 校验
		newPhone(e) {
			console.log(e.detail.value)

		},
		// 获取验证码
		getCode() {
			if (this.newMobile != '') {
				var reg = /^(1[0-9])\d{9}$/;
				if (!reg.test(this.newMobile)) {
					Toast.fail('请输入正确的手机号码');
				} else {
					this.disabled = true;
					this.code = this.num + 's';
					var timer = setInterval(() => {
						this.num--;
						this.code = this.num + 's';
						if (this.num == 0) {
							clearInterval(timer);
							this.code = '重新发送';
							this.num = 60;
							this.disabled = false;
						}
					}, 1000)
				}
			} else {
				Toast.fail('请输入手机号码');
			}
		},
		save() {
			console.log(this.newCode)
		}
	}
}
